import util from '../../common/util.js'
export default {
	data() {
		return {
			url:'',
			orderId:0,
			order:''
		}
	},
	onLoad(e) {
		var _this = this;
		this.orderId = e.orderId;
		this.url = this.serverURL;
		
		
	},
	onShow() {
		var _this = this;
		uni.showLoading({
			title:'加载中...',
			mask:true
		})
		uni.request({
			url: _this.url + 'home/user/user_order_id',
			method:'POST',
			data:{
				id: _this.orderId
			},
			success(res) {
				console.log(res);
				if(res.data.code==100){
					var order = res.data.msg;
					_this.order = order;
				}
				uni.hideLoading();
			},
			fail(error) {
				console.log(error)
			}
		})
	},
	filters:{
		filterTime(time) {
			return util.formatDate(time);
		}
	},
	methods: {
		seePhoto(url) {
			uni.navigateTo({
				url:'../photo/photo?photo='+url
			})
		},
		isConfirm() {
			var _this = this;
			uni.showLoading({
				title:'订单完成中...',
				mask:true
			})
			uni.request({
				url: _this.url + 'home/Order/update_transportation_order',
				method:'POST',
				data:{
					id: _this.orderId,
					states: 5,
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						uni.hideLoading();
						_this.order.states = 5;
						uni.showToast({
							title:'订单已完成',
							icon:"success",
							mask:true
						})
					}
				},
				fail(error) {
					console.log(error);
					uni.showToast({
						title:'修改失败',
						icon:"none",
						mask:true
					})
				}
			})
		},
		goAssess() {
			uni.navigateTo({
				url: '../writeAassess/writeAassess?orderId='+this.orderId+"&name="+this.order.username
			})
		}
	}
}
